import { apiService, ApiResponse } from './api';

export interface DashboardOverview {
  total_users: number;
  total_sessions: number;
  total_conversions: number;
  average_conversion_rate: number;
  total_revenue?: number;
  average_order_value?: number;
  total_leads?: number;
  lead_conversion_rate?: number;
  total_page_views?: number;
  unique_visitors?: number;
}

export interface FunnelData {
  scenario_name: string;
  stage_name: string;
  stage_order: number;
  users_reached: number;
  conversion_rate: number;
}

export interface KPITrend {
  date: string;
  value: number;
  revenue?: number;
  leads?: number;
  page_views?: number;
}

export interface RecentEvent {
  user_id: string;
  action_type: string;
  performed_at: string;
}

export interface ScenarioPerformance {
  scenario_name: string;
  conversion_rate: number;
  total_users: number;
  revenue?: number;
  leads?: number;
  page_views?: number;
}

export interface CategoryMetrics {
  ecommerce: {
    total_revenue: number;
    average_order_value: number;
    cart_abandonment_rate: number;
    conversion_rate: number;
    customer_lifetime_value: number;
    repeat_purchase_rate: number;
    product_view_to_purchase_rate: number;
    checkout_completion_rate: number;
  };
  lead_generation: {
    total_leads: number;
    lead_conversion_rate: number;
    cost_per_lead: number;
    lead_quality_score: number;
    form_completion_rate: number;
    email_open_rate: number;
    click_through_rate: number;
    lead_to_customer_rate: number;
  };
  general_website: {
    total_page_views: number;
    unique_visitors: number;
    bounce_rate: number;
    average_session_duration: number;
    pages_per_session: number;
    return_visitor_rate: number;
    mobile_traffic_percentage: number;
    top_exit_pages: string[];
  };
}

export type CategoryType = 'ecommerce' | 'lead_generation' | 'general_website' | 'all';

export class DashboardService {
  //   
  static async getCategoryMetrics(category?: CategoryType): Promise<ApiResponse<CategoryMetrics | Partial<CategoryMetrics>>> {
    const params = category && category !== 'all' ? { category } : undefined;
    return apiService.get<CategoryMetrics | Partial<CategoryMetrics>>('/dashboard/category-metrics', params);
  }

  //    
  static async getOverview(category?: CategoryType): Promise<ApiResponse<DashboardOverview>> {
    const params = category && category !== 'all' ? { category } : undefined;
    return apiService.get<DashboardOverview>('/dashboard/overview', params);
  }

  //   
  static async getFunnels(scenarioId?: string, category?: CategoryType): Promise<ApiResponse<FunnelData[]>> {
    const params: Record<string, string> = {};
    if (scenarioId) params.scenario_id = scenarioId;
    if (category && category !== 'all') params.category = category;
    
    return apiService.get<FunnelData[]>('/dashboard/funnels', Object.keys(params).length > 0 ? params : undefined);
  }

  // KPI   
  static async getKPITrends(metric?: string, period?: string, category?: CategoryType): Promise<ApiResponse<KPITrend[]>> {
    const params: Record<string, string> = {};
    if (metric) params.metric = metric;
    if (period) params.period = period;
    if (category && category !== 'all') params.category = category;
    
    return apiService.get<KPITrend[]>('/dashboard/kpi-trends', Object.keys(params).length > 0 ? params : undefined);
  }

  //    
  static async getRecentEvents(limit?: string, category?: CategoryType): Promise<ApiResponse<RecentEvent[]>> {
    const params: Record<string, string> = {};
    if (limit) params.limit = limit;
    if (category && category !== 'all') params.category = category;
    
    return apiService.get<RecentEvent[]>('/dashboard/recent-events', Object.keys(params).length > 0 ? params : undefined);
  }

  //    
  static async getScenarioPerformance(category?: CategoryType): Promise<ApiResponse<ScenarioPerformance[]>> {
    const params = category && category !== 'all' ? { category } : undefined;
    return apiService.get<ScenarioPerformance[]>('/dashboard/scenario-performance', params);
  }
} 