import React from 'react';
import { CategoryType } from '../services/dashboardService';

interface CategorySelectorProps {
  selectedCategory: CategoryType;
  onCategoryChange: (category: CategoryType) => void;
}

const CategorySelector: React.FC<CategorySelectorProps> = ({ selectedCategory, onCategoryChange }) => {
  const categories = [
    { value: 'all', label: '', description: '  ' },
    { value: 'ecommerce', label: 'E-commerce', description: '  ' },
    { value: 'lead_generation', label: ' ', description: '    ' },
    { value: 'general_website', label: ' ', description: '   ' }
  ] as const;

  return (
    <div className="mb-6">
      <div className="flex flex-col sm:flex-row gap-4">
        {categories.map((category) => (
          <button
            key={category.value}
            onClick={() => onCategoryChange(category.value as CategoryType)}
            className={`flex-1 p-4 rounded-lg border-2 transition-all duration-200 ${
              selectedCategory === category.value
                ? 'border-blue-500 bg-blue-50 text-blue-700'
                : 'border-gray-200 bg-white text-gray-700 hover:border-gray-300 hover:bg-gray-50'
            }`}
          >
            <div className="text-left">
              <div className="font-semibold text-lg mb-1">{category.label}</div>
              <div className="text-sm opacity-75">{category.description}</div>
            </div>
          </button>
        ))}
      </div>
    </div>
  );
};

export default CategorySelector; 