import React, { useState, useEffect } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';
import { Line, Bar } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  Filler
);

interface KPIData {
  name: string;
  category: string;
  current_value: number;
  target_value: number;
  unit: string;
  trend: number[];
  dates: string[];
  journeyStage?: string; //    
  impact?: 'high' | 'medium' | 'low'; //   
  description?: string; // KPI 
  improvement?: string; //  
}

const KPIAnalytics: React.FC = () => {
  const [kpiData, setKpiData] = useState<KPIData[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState<string>('all');
  const [selectedKPI, setSelectedKPI] = useState<KPIData | null>(null);
  const [showJourneyMap, setShowJourneyMap] = useState(false);

  //     
  const toggleJourneyMap = () => {
    console.log('   .  :', showJourneyMap);
    setShowJourneyMap(prev => {
      const newState = !prev;
      console.log(' :', newState);
      return newState;
    });
  };

  useEffect(() => {
    //  KPI 
    const mockKPIData: KPIData[] = [
      {
        name: '',
        category: 'conversion',
        current_value: 12.5,
        target_value: 15.0,
        unit: '%',
        trend: [10.2, 11.1, 12.3, 11.8, 13.2, 12.5],
        dates: ['2025-08-01', '2025-08-02', '2025-08-03', '2025-08-04', '2025-08-05', '2025-08-06'],
        journeyStage: ' ',
        impact: 'high',
        description: '       ',
        improvement: '  ,   '
      },
      {
        name: ' ',
        category: 'engagement',
        current_value: 180,
        target_value: 200,
        unit: '',
        trend: [165, 172, 185, 178, 190, 180],
        dates: ['2025-08-01', '2025-08-02', '2025-08-03', '2025-08-04', '2025-08-05', '2025-08-06'],
        journeyStage: ' ',
        impact: 'medium',
        description: '     ',
        improvement: '  ,   '
      },
      {
        name: '',
        category: 'retention',
        current_value: 28.5,
        target_value: 30.0,
        unit: '%',
        trend: [25.2, 26.8, 27.5, 28.1, 29.2, 28.5],
        dates: ['2025-08-01', '2025-08-02', '2025-08-03', '2025-08-04', '2025-08-05', '2025-08-06'],
        journeyStage: ' ',
        impact: 'high',
        description: '    ',
        improvement: '  ,   '
      },
      {
        name: '  ',
        category: 'revenue',
        current_value: 45000,
        target_value: 50000,
        unit: '',
        trend: [42000, 43500, 44500, 43800, 46000, 45000],
        dates: ['2025-08-01', '2025-08-02', '2025-08-03', '2025-08-04', '2025-08-05', '2025-08-06'],
        journeyStage: ' ',
        impact: 'high',
        description: '      ',
        improvement: '  ,  '
      }
    ];

    setTimeout(() => {
      setKpiData(mockKPIData);
      setLoading(false);
    }, 1000);
  }, []);

  const categories = [
    { id: 'all', name: '' },
    { id: 'conversion', name: '' },
    { id: 'engagement', name: '' },
    { id: 'retention', name: '' },
    { id: 'revenue', name: '' }
  ];

  const filteredKPIs = selectedCategory === 'all' 
    ? kpiData 
    : kpiData.filter(kpi => kpi.category === selectedCategory);

  const getProgressPercentage = (current: number, target: number) => {
    return Math.min((current / target) * 100, 100);
  };

  const getProgressColor = (percentage: number) => {
    if (percentage >= 90) return 'bg-green-500';
    if (percentage >= 70) return 'bg-yellow-500';
    return 'bg-red-500';
  };

  const getTrendColor = (current: number, previous: number) => {
    return current >= previous ? 'text-green-600' : 'text-red-600';
  };

  const getImpactColor = (impact: string) => {
    switch (impact) {
      case 'high': return 'bg-red-100 text-red-700';
      case 'medium': return 'bg-yellow-100 text-yellow-700';
      case 'low': return 'bg-green-100 text-green-700';
      default: return 'bg-gray-100 text-gray-700';
    }
  };

  const getJourneyStageColor = (stage: string) => {
    switch (stage) {
      case ' ': return 'bg-blue-100 text-blue-700';
      case ' ': return 'bg-yellow-100 text-yellow-700';
      case ' ': return 'bg-green-100 text-green-700';
      case ' ': return 'bg-purple-100 text-purple-700';
      case ' ': return 'bg-indigo-100 text-indigo-700';
      case ' ': return 'bg-gray-100 text-gray-700';
      default: return 'bg-gray-100 text-gray-700';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">KPI </h1>
        <p className="text-gray-600">
                
        </p>
      </div>

      {/*       */}
      <div className="flex justify-between items-center mb-6">
        <div className="flex space-x-2">
          {categories.map((category) => (
            <button
              key={category.id}
              onClick={() => setSelectedCategory(category.id)}
              className={`px-4 py-2 rounded-md text-sm font-medium transition-colors duration-200 ${
                selectedCategory === category.id
                  ? 'bg-primary-600 text-white'
                  : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
              }`}
            >
              {category.name}
            </button>
          ))}
        </div>
        
        <button
          onClick={toggleJourneyMap}
          className="flex items-center px-4 py-2 bg-purple-600 text-white rounded-md text-sm font-medium hover:bg-purple-700 transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-purple-500 focus:ring-offset-2"
        >
          <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 20l-5.447-2.724A1 1 0 013 16.382V5.618a1 1 0 011.447-.894L9 7m0 13l6-3m-6 3V7m6 10l4.553 2.276A1 1 0 0021 18.382V7.618a1 1 0 00-1.447-.894L15 4m0 13V4m0 0L9 7" />
          </svg>
          {showJourneyMap ? '  ' : '  '}
        </button>
      </div>

      {/*      */}
      {showJourneyMap && (
        <div className="bg-white rounded-lg shadow-sm border p-6 mb-8 animate-fade-in">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">   </h3>
          <div className="grid grid-cols-5 gap-4">
            {[' ', ' ', ' ', ' ', ' '].map((stage, index) => {
              const stageKPIs = filteredKPIs.filter(kpi => kpi.journeyStage === stage);
              const avgProgress = stageKPIs.length > 0 
                ? stageKPIs.reduce((sum, kpi) => sum + getProgressPercentage(kpi.current_value, kpi.target_value), 0) / stageKPIs.length
                : 0;
              
              return (
                <div key={stage} className="text-center p-4 border rounded-lg hover:bg-gray-50 transition-colors cursor-pointer">
                  <div className="text-sm font-medium text-gray-700 mb-2">{stage}</div>
                  <div className="text-2xl font-bold text-gray-900 mb-2">{avgProgress.toFixed(0)}%</div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div
                      className={`h-2 rounded-full transition-all duration-300 ${
                        avgProgress >= 90 ? 'bg-green-500' :
                        avgProgress >= 70 ? 'bg-yellow-500' : 'bg-red-500'
                      }`}
                      style={{ width: `${Math.min(avgProgress, 100)}%` }}
                    ></div>
                  </div>
                  <div className="text-xs text-gray-500 mt-1">
                    {stageKPIs.length} KPI
                  </div>
                </div>
              );
            })}
          </div>
          
          <div className="mt-4 p-3 bg-blue-50 border border-blue-200 rounded-lg">
            <div className="flex items-start">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-blue-400" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="ml-3">
                <h4 className="text-sm font-medium text-blue-800">  KPI </h4>
                <p className="text-sm text-blue-700 mt-1">
                       KPI     . 
                         .
                </p>
              </div>
            </div>
          </div>
        </div>
      )}

      {/*     KPI  */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
        {filteredKPIs.map((kpi) => {
          const progressPercentage = getProgressPercentage(kpi.current_value, kpi.target_value);
          const trendChange = kpi.trend.length > 1 
            ? ((kpi.trend[kpi.trend.length - 1] - kpi.trend[kpi.trend.length - 2]) / kpi.trend[kpi.trend.length - 2] * 100)
            : 0;

          return (
            <div 
              key={kpi.name} 
              className="bg-white rounded-lg shadow-sm border p-6 hover:shadow-md transition-shadow duration-200 cursor-pointer"
              onClick={() => setSelectedKPI(kpi)}
            >
              {/*  */}
              <div className="flex justify-between items-start mb-4">
                <div>
                  <h3 className="text-lg font-semibold text-gray-900">{kpi.name}</h3>
                  <p className="text-sm text-gray-600 capitalize">{kpi.category}</p>
                </div>
                <div className="flex space-x-2">
                  {kpi.impact && (
                    <span className={`px-2 py-1 rounded text-xs font-medium ${getImpactColor(kpi.impact)}`}>
                      {kpi.impact === 'high' ? '' : kpi.impact === 'medium' ? '' : ''}
                    </span>
                  )}
                  <span className={`text-sm font-medium ${getTrendColor(kpi.trend[kpi.trend.length - 1], kpi.trend[kpi.trend.length - 2])}`}>
                    {trendChange >= 0 ? '+' : ''}{trendChange.toFixed(1)}%
                  </span>
                </div>
              </div>

              {/*     */}
              {kpi.journeyStage && (
                <div className="mb-4">
                  <span className={`inline-block px-2 py-1 rounded text-xs font-medium ${getJourneyStageColor(kpi.journeyStage)}`}>
                    {kpi.journeyStage}
                  </span>
                </div>
              )}

              {/*   */}
              <div className="mb-4">
                <div className="flex items-baseline">
                  <span className="text-3xl font-bold text-gray-900">
                    {kpi.current_value.toLocaleString()}
                  </span>
                  <span className="ml-1 text-sm text-gray-600">{kpi.unit}</span>
                </div>
                <div className="text-sm text-gray-600">
                  : {kpi.target_value.toLocaleString()} {kpi.unit}
                </div>
              </div>

              {/*   */}
              <div className="mb-4">
                <div className="flex justify-between text-sm text-gray-600 mb-1">
                  <span> </span>
                  <span>{progressPercentage.toFixed(1)}%</span>
                </div>
                <div className="w-full bg-gray-200 rounded-full h-2">
                  <div
                    className={`h-2 rounded-full transition-all duration-300 ${getProgressColor(progressPercentage)}`}
                    style={{ width: `${progressPercentage}%` }}
                  ></div>
                </div>
              </div>

              {/* KPI  */}
              {kpi.description && (
                <div className="mb-4 p-3 bg-gray-50 rounded-lg">
                  <p className="text-sm text-gray-700">{kpi.description}</p>
                </div>
              )}

              {/*   */}
              {kpi.improvement && (
                <div className="flex items-center text-sm text-blue-600">
                  <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  <span>   </span>
                </div>
              )}
            </div>
          );
        })}
      </div>

      {/*  KPI    */}
      {selectedKPI && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl max-w-4xl w-full mx-4 max-h-[90vh] overflow-y-auto">
            <div className="p-6">
              {/*   */}
              <div className="flex justify-between items-start mb-6">
                <div>
                  <h2 className="text-2xl font-bold text-gray-900">{selectedKPI.name}  </h2>
                  <p className="text-gray-600 mt-1">   </p>
                </div>
                <button
                  onClick={() => setSelectedKPI(null)}
                  className="text-gray-400 hover:text-gray-600 transition-colors"
                >
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>

              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* KPI   */}
                <div className="space-y-6">
                  <div className="bg-blue-50 p-4 rounded-lg">
                    <h3 className="text-lg font-semibold text-blue-900 mb-3">KPI </h3>
                    <div className="space-y-3">
                      <div className="flex justify-between">
                        <span className="text-blue-700"> :</span>
                        <span className="font-semibold">{selectedKPI.current_value.toLocaleString()} {selectedKPI.unit}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-blue-700"> :</span>
                        <span className="font-semibold">{selectedKPI.target_value.toLocaleString()} {selectedKPI.unit}</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-blue-700">:</span>
                        <span className="font-semibold">{getProgressPercentage(selectedKPI.current_value, selectedKPI.target_value).toFixed(1)}%</span>
                      </div>
                      {selectedKPI.impact && (
                        <div className="flex justify-between">
                          <span className="text-blue-700"> :</span>
                          <span className={`px-2 py-1 rounded text-xs font-medium ${getImpactColor(selectedKPI.impact)}`}>
                            {selectedKPI.impact === 'high' ? '' : selectedKPI.impact === 'medium' ? '' : ''}
                          </span>
                        </div>
                      )}
                    </div>
                  </div>

                  {/*    */}
                  {selectedKPI.journeyStage && (
                    <div className="bg-green-50 p-4 rounded-lg">
                      <h3 className="text-lg font-semibold text-green-900 mb-3">  </h3>
                      <div className="space-y-3">
                        <div>
                          <span className={`inline-block px-3 py-1 rounded text-sm font-medium ${getJourneyStageColor(selectedKPI.journeyStage)}`}>
                            {selectedKPI.journeyStage}
                          </span>
                        </div>
                        <p className="text-sm text-green-700">
                           KPI {selectedKPI.journeyStage}     .
                        </p>
                      </div>
                    </div>
                  )}

                  {/*   */}
                  {selectedKPI.improvement && (
                    <div className="bg-yellow-50 p-4 rounded-lg">
                      <h3 className="text-lg font-semibold text-yellow-900 mb-3"> </h3>
                      <p className="text-sm text-yellow-700">{selectedKPI.improvement}</p>
                    </div>
                  )}
                </div>

                {/*   */}
                <div className="bg-white border rounded-lg p-4">
                  <h3 className="text-lg font-semibold text-gray-900 mb-4">{selectedKPI.name} </h3>
                  <div className="h-64">
                    <Line
                      data={{
                        labels: selectedKPI.dates.map(date => 
                          new Date(date).toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' })
                        ),
                        datasets: [
                          {
                            label: selectedKPI.name,
                            data: selectedKPI.trend,
                            borderColor: 'rgb(59, 130, 246)',
                            backgroundColor: 'rgba(59, 130, 246, 0.1)',
                            borderWidth: 2,
                            fill: true,
                            tension: 0.4,
                          },
                          {
                            label: '',
                            data: Array(selectedKPI.trend.length).fill(selectedKPI.target_value),
                            borderColor: 'rgb(239, 68, 68)',
                            backgroundColor: 'transparent',
                            borderWidth: 2,
                            borderDash: [5, 5],
                            fill: false,
                          }
                        ]
                      }}
                      options={{
                        responsive: true,
                        maintainAspectRatio: false,
                        plugins: {
                          legend: {
                            position: 'top' as const,
                          },
                          tooltip: {
                            callbacks: {
                              label: function(context: any) {
                                return `${context.dataset.label}: ${context.parsed.y.toLocaleString()} ${selectedKPI.unit}`;
                              },
                            },
                          },
                        },
                        scales: {
                          y: {
                            beginAtZero: true,
                            grid: {
                              color: 'rgba(0, 0, 0, 0.1)',
                            },
                          },
                          x: {
                            grid: {
                              display: false,
                            },
                          },
                        },
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* KPI   */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {filteredKPIs.slice(0, 2).map((kpi) => (
          <div key={kpi.name} className="chart-container">
            <h3 className="chart-title">{kpi.name} </h3>
            <div className="h-64">
              <Line
                data={{
                  labels: kpi.dates.map(date => 
                    new Date(date).toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' })
                  ),
                  datasets: [
                    {
                      label: kpi.name,
                      data: kpi.trend,
                      borderColor: 'rgb(59, 130, 246)',
                      backgroundColor: 'rgba(59, 130, 246, 0.1)',
                      borderWidth: 2,
                      fill: true,
                      tension: 0.4,
                    },
                    {
                      label: '',
                      data: Array(kpi.dates.length).fill(kpi.target_value),
                      borderColor: 'rgb(239, 68, 68)',
                      backgroundColor: 'rgba(239, 68, 68, 0.1)',
                      borderWidth: 2,
                      borderDash: [5, 5],
                      fill: false,
                    }
                  ]
                }}
                options={{
                  responsive: true,
                  maintainAspectRatio: false,
                  plugins: {
                    legend: {
                      position: 'top' as const,
                    },
                    tooltip: {
                      callbacks: {
                        label: function(context: any) {
                          return `${context.dataset.label}: ${context.parsed.y.toLocaleString()} ${kpi.unit}`;
                        },
                      },
                    },
                  },
                  scales: {
                    y: {
                      beginAtZero: true,
                      ticks: {
                        callback: function(value: any) {
                          return value.toLocaleString() + (kpi.unit !== '%' ? ' ' + kpi.unit : '%');
                        },
                      },
                    },
                  },
                }}
              />
            </div>
          </div>
        ))}
      </div>

      {/* KPI    */}
      <div className="chart-container">
        <h3 className="chart-title">KPI  </h3>
        <div className="h-64">
          <Bar
            data={{
              labels: filteredKPIs.map(kpi => kpi.name),
              datasets: [
                {
                  label: ' ',
                  data: filteredKPIs.map(kpi => {
                    //  KPI 0-100  
                    const maxValue = Math.max(kpi.current_value, kpi.target_value);
                    return (kpi.current_value / maxValue) * 100;
                  }),
                  backgroundColor: 'rgba(59, 130, 246, 0.8)',
                  borderColor: 'rgb(59, 130, 246)',
                  borderWidth: 1,
                  borderRadius: 4,
                },
                {
                  label: ' ',
                  data: filteredKPIs.map(kpi => {
                    //  KPI 0-100  
                    const maxValue = Math.max(kpi.current_value, kpi.target_value);
                    return (kpi.target_value / maxValue) * 100;
                  }),
                  backgroundColor: 'rgba(239, 68, 68, 0.8)',
                  borderColor: 'rgb(239, 68, 68)',
                  borderWidth: 1,
                  borderRadius: 4,
                }
              ]
            }}
            options={{
              responsive: true,
              maintainAspectRatio: false,
              plugins: {
                legend: {
                  position: 'top' as const,
                  labels: {
                    usePointStyle: true,
                    padding: 20,
                  },
                },
                tooltip: {
                  callbacks: {
                    label: function(context: any) {
                                             const kpi = filteredKPIs[context.dataIndex];
                       
                       if (context.dataset.label === ' ') {
                         return ` : ${kpi.current_value.toLocaleString()} ${kpi.unit}`;
                       } else {
                         return ` : ${kpi.target_value.toLocaleString()} ${kpi.unit}`;
                       }
                    },
                  },
                },
              },
              scales: {
                y: {
                  beginAtZero: true,
                  max: 100,
                  grid: {
                    color: 'rgba(0, 0, 0, 0.1)',
                  },
                  ticks: {
                    callback: function(value: any) {
                      return value.toFixed(0) + '%';
                    },
                    font: {
                      size: 12,
                    },
                  },
                },
                x: {
                  grid: {
                    display: false,
                  },
                  ticks: {
                    font: {
                      size: 12,
                    },
                  },
                },
              },
              elements: {
                bar: {
                  borderSkipped: false,
                },
              },
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default KPIAnalytics; 