import React, { useState } from 'react';

interface IntegrationSettingsProps {
  settings: {
    google_analytics: {
      enabled: boolean;
      tracking_id: string;
      custom_dimensions: string[];
    };
    facebook_pixel: {
      enabled: boolean;
      pixel_id: string;
    };
    google_ads: {
      enabled: boolean;
      conversion_tracking: boolean;
    };
    crm_system: {
      enabled: boolean;
      system_type: 'salesforce' | 'hubspot' | 'pipedrive' | 'custom';
      api_endpoint: string;
      api_key: string;
    };
  };
  onSave: (settings: any) => void;
  saving: boolean;
}

const IntegrationSettings: React.FC<IntegrationSettingsProps> = ({ settings, onSave, saving }) => {
  const [formData, setFormData] = useState(settings);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(formData);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Google Analytics */}
      <div className="border border-gray-200 rounded-lg p-4">
        <div className="flex items-center mb-4">
          <input
            type="checkbox"
            id="ga_enabled"
            checked={formData.google_analytics.enabled}
            onChange={(e) => setFormData({
              ...formData,
              google_analytics: {
                ...formData.google_analytics,
                enabled: e.target.checked
              }
            })}
            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
          <label htmlFor="ga_enabled" className="ml-2 block text-sm font-medium text-gray-900">
            Google Analytics
          </label>
        </div>
        
        {formData.google_analytics.enabled && (
          <div className="ml-6 space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 ID
              </label>
              <input
                type="text"
                value={formData.google_analytics.tracking_id}
                onChange={(e) => setFormData({
                  ...formData,
                  google_analytics: {
                    ...formData.google_analytics,
                    tracking_id: e.target.value
                  }
                })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="GA-XXXXXXXXX"
              />
            </div>
          </div>
        )}
      </div>

      {/* Facebook Pixel */}
      <div className="border border-gray-200 rounded-lg p-4">
        <div className="flex items-center mb-4">
          <input
            type="checkbox"
            id="fb_enabled"
            checked={formData.facebook_pixel.enabled}
            onChange={(e) => setFormData({
              ...formData,
              facebook_pixel: {
                ...formData.facebook_pixel,
                enabled: e.target.checked
              }
            })}
            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
          <label htmlFor="fb_enabled" className="ml-2 block text-sm font-medium text-gray-900">
            Facebook Pixel
          </label>
        </div>
        
        {formData.facebook_pixel.enabled && (
          <div className="ml-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Pixel ID
              </label>
              <input
                type="text"
                value={formData.facebook_pixel.pixel_id}
                onChange={(e) => setFormData({
                  ...formData,
                  facebook_pixel: {
                    ...formData.facebook_pixel,
                    pixel_id: e.target.value
                  }
                })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="123456789012345"
              />
            </div>
          </div>
        )}
      </div>

      {/* Google Ads */}
      <div className="border border-gray-200 rounded-lg p-4">
        <div className="flex items-center mb-4">
          <input
            type="checkbox"
            id="gads_enabled"
            checked={formData.google_ads.enabled}
            onChange={(e) => setFormData({
              ...formData,
              google_ads: {
                ...formData.google_ads,
                enabled: e.target.checked
              }
            })}
            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
          <label htmlFor="gads_enabled" className="ml-2 block text-sm font-medium text-gray-900">
            Google Ads
          </label>
        </div>
        
        {formData.google_ads.enabled && (
          <div className="ml-6">
            <div className="flex items-center">
              <input
                type="checkbox"
                id="conversion_tracking"
                checked={formData.google_ads.conversion_tracking}
                onChange={(e) => setFormData({
                  ...formData,
                  google_ads: {
                    ...formData.google_ads,
                    conversion_tracking: e.target.checked
                  }
                })}
                className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
              />
              <label htmlFor="conversion_tracking" className="ml-2 block text-sm text-gray-900">
                  
              </label>
            </div>
          </div>
        )}
      </div>

      {/* CRM System */}
      <div className="border border-gray-200 rounded-lg p-4">
        <div className="flex items-center mb-4">
          <input
            type="checkbox"
            id="crm_enabled"
            checked={formData.crm_system.enabled}
            onChange={(e) => setFormData({
              ...formData,
              crm_system: {
                ...formData.crm_system,
                enabled: e.target.checked
              }
            })}
            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
          <label htmlFor="crm_enabled" className="ml-2 block text-sm font-medium text-gray-900">
            CRM 
          </label>
        </div>
        
        {formData.crm_system.enabled && (
          <div className="ml-6 space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                CRM 
              </label>
              <select
                value={formData.crm_system.system_type}
                onChange={(e) => setFormData({
                  ...formData,
                  crm_system: {
                    ...formData.crm_system,
                    system_type: e.target.value as any
                  }
                })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="salesforce">Salesforce</option>
                <option value="hubspot">HubSpot</option>
                <option value="pipedrive">Pipedrive</option>
                <option value="custom"></option>
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                API 
              </label>
              <input
                type="url"
                value={formData.crm_system.api_endpoint}
                onChange={(e) => setFormData({
                  ...formData,
                  crm_system: {
                    ...formData.crm_system,
                    api_endpoint: e.target.value
                  }
                })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="https://api.example.com"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                API 
              </label>
              <input
                type="password"
                value={formData.crm_system.api_key}
                onChange={(e) => setFormData({
                  ...formData,
                  crm_system: {
                    ...formData.crm_system,
                    api_key: e.target.value
                  }
                })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                placeholder="API  "
              />
            </div>
          </div>
        )}
      </div>

      <div className="flex justify-end">
        <button
          type="submit"
          disabled={saving}
          className="btn-primary disabled:opacity-50"
        >
          {saving ? ' ...' : ''}
        </button>
      </div>
    </form>
  );
};

export default IntegrationSettings; 