import React, { useState } from 'react';

interface DashboardSettingsProps {
  settings: {
    default_category: 'all' | 'ecommerce' | 'lead_generation' | 'general_website';
    chart_theme: 'light' | 'dark' | 'auto';
    show_real_time_data: boolean;
    max_data_points: number;
    enable_export: boolean;
    enable_sharing: boolean;
  };
  onSave: (settings: any) => void;
  saving: boolean;
}

const DashboardSettings: React.FC<DashboardSettingsProps> = ({ settings, onSave, saving }) => {
  const [formData, setFormData] = useState(settings);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(formData);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
             
          </label>
          <select
            value={formData.default_category}
            onChange={(e) => setFormData({ ...formData, default_category: e.target.value as any })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="all"></option>
            <option value="ecommerce">E-commerce</option>
            <option value="lead_generation"> </option>
            <option value="general_website"> </option>
          </select>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
             
          </label>
          <select
            value={formData.chart_theme}
            onChange={(e) => setFormData({ ...formData, chart_theme: e.target.value as any })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="light"></option>
            <option value="dark"></option>
            <option value="auto"></option>
          </select>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
              
          </label>
          <input
            type="number"
            value={formData.max_data_points}
            onChange={(e) => setFormData({ ...formData, max_data_points: parseInt(e.target.value) })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            min="100"
            max="10000"
            required
          />
        </div>

        <div className="space-y-4">
          <div className="flex items-center">
            <input
              type="checkbox"
              id="show_real_time_data"
              checked={formData.show_real_time_data}
              onChange={(e) => setFormData({ ...formData, show_real_time_data: e.target.checked })}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
            <label htmlFor="show_real_time_data" className="ml-2 block text-sm text-gray-900">
                
            </label>
          </div>

          <div className="flex items-center">
            <input
              type="checkbox"
              id="enable_export"
              checked={formData.enable_export}
              onChange={(e) => setFormData({ ...formData, enable_export: e.target.checked })}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
            <label htmlFor="enable_export" className="ml-2 block text-sm text-gray-900">
                
            </label>
          </div>

          <div className="flex items-center">
            <input
              type="checkbox"
              id="enable_sharing"
              checked={formData.enable_sharing}
              onChange={(e) => setFormData({ ...formData, enable_sharing: e.target.checked })}
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
            />
            <label htmlFor="enable_sharing" className="ml-2 block text-sm text-gray-900">
                
            </label>
          </div>
        </div>
      </div>

      <div className="flex justify-end">
        <button
          type="submit"
          disabled={saving}
          className="btn-primary disabled:opacity-50"
        >
          {saving ? ' ...' : ''}
        </button>
      </div>
    </form>
  );
};

export default DashboardSettings; 