import React, { useState, useEffect } from 'react';
import { SettingService, SystemSettings, SettingCategory } from '../services/settingService';
import LoadingSpinner from './LoadingSpinner';
import ErrorMessage from './ErrorMessage';
import GeneralSettings from './settings/GeneralSettings';
import DashboardSettings from './settings/DashboardSettings';
import NotificationSettings from './settings/NotificationSettings';
import IntegrationSettings from './settings/IntegrationSettings';
import SecuritySettings from './settings/SecuritySettings';
import DataProcessingSettings from './settings/DataProcessingSettings';

const Settings: React.FC = () => {
  const [settings, setSettings] = useState<SystemSettings | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [activeCategory, setActiveCategory] = useState<SettingCategory>('general');
  const [saving, setSaving] = useState(false);
  const [saveMessage, setSaveMessage] = useState<{ type: 'success' | 'error'; message: string } | null>(null);

  useEffect(() => {
    loadSettings();
  }, []);

  const loadSettings = async () => {
    try {
      setLoading(true);
      setError(null);
      const response = await SettingService.getAllSettings();
      
      if (response.success && response.data) {
        setSettings(response.data);
      } else {
        setError('  .');
      }
    } catch (err) {
      setError('    .');
      console.error('Settings load error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleSaveSettings = async (category: SettingCategory, updatedSettings: any) => {
    try {
      setSaving(true);
      setSaveMessage(null);

      //  
      const validation = SettingService.validateSettings(category, updatedSettings);
      if (!validation.isValid) {
        setSaveMessage({
          type: 'error',
          message: `  : ${validation.errors.join(', ')}`
        });
        return;
      }

      const response = await SettingService.updateSettings(category, updatedSettings);
      
      if (response.success) {
        setSettings(prev => prev ? {
          ...prev,
          [category]: { ...prev[category], ...updatedSettings }
        } : null);
        
        setSaveMessage({
          type: 'success',
          message: `${SettingService.getCategoryDescription(category)}   .`
        });

        // 3   
        setTimeout(() => setSaveMessage(null), 3000);
      } else {
        setSaveMessage({
          type: 'error',
          message: response.error || '  .'
        });
      }
    } catch (err) {
      setSaveMessage({
        type: 'error',
        message: '    .'
      });
      console.error('Settings save error:', err);
    } finally {
      setSaving(false);
    }
  };

  const handleResetSettings = async (category?: SettingCategory) => {
    if (!window.confirm(`${category ? SettingService.getCategoryDescription(category) : ''}  ?`)) {
      return;
    }

    try {
      setSaving(true);
      const response = await SettingService.resetSettings(category);
      
      if (response.success) {
        await loadSettings(); //    
        setSaveMessage({
          type: 'success',
          message: `${category ? SettingService.getCategoryDescription(category) : ''}  .`
        });
        setTimeout(() => setSaveMessage(null), 3000);
      } else {
        setSaveMessage({
          type: 'error',
          message: response.error || '  .'
        });
      }
    } catch (err) {
      setSaveMessage({
        type: 'error',
        message: '    .'
      });
      console.error('Settings reset error:', err);
    } finally {
      setSaving(false);
    }
  };

  const handleExportSettings = async () => {
    try {
      const blob = await SettingService.exportSettings('json');
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'system-settings.json';
      document.body.appendChild(a);
      a.click();
      window.URL.revokeObjectURL(url);
      document.body.removeChild(a);
    } catch (err) {
      setSaveMessage({
        type: 'error',
        message: '  .'
      });
      console.error('Settings export error:', err);
    }
  };

  const categories: { key: SettingCategory; label: string; icon: string }[] = [
    { key: 'general', label: '', icon: SettingService.getCategoryIcon('general') },
    { key: 'dashboard', label: '', icon: SettingService.getCategoryIcon('dashboard') },
    { key: 'notifications', label: '', icon: SettingService.getCategoryIcon('notifications') },
    { key: 'integrations', label: '', icon: SettingService.getCategoryIcon('integrations') },
    { key: 'security', label: '', icon: SettingService.getCategoryIcon('security') },
    { key: 'data_processing', label: ' ', icon: SettingService.getCategoryIcon('data_processing') }
  ];

  const renderSettingsContent = () => {
    if (!settings) return null;

    switch (activeCategory) {
      case 'general':
        return (
          <GeneralSettings
            settings={settings.general}
            onSave={(updatedSettings) => handleSaveSettings('general', updatedSettings)}
            saving={saving}
          />
        );
      case 'dashboard':
        return (
          <DashboardSettings
            settings={settings.dashboard}
            onSave={(updatedSettings) => handleSaveSettings('dashboard', updatedSettings)}
            saving={saving}
          />
        );
      case 'notifications':
        return (
          <NotificationSettings
            settings={settings.notifications}
            onSave={(updatedSettings) => handleSaveSettings('notifications', updatedSettings)}
            saving={saving}
          />
        );
      case 'integrations':
        return (
          <IntegrationSettings
            settings={settings.integrations}
            onSave={(updatedSettings) => handleSaveSettings('integrations', updatedSettings)}
            saving={saving}
          />
        );
      case 'security':
        return (
          <SecuritySettings
            settings={settings.security}
            onSave={(updatedSettings) => handleSaveSettings('security', updatedSettings)}
            saving={saving}
          />
        );
      case 'data_processing':
        return (
          <DataProcessingSettings
            settings={settings.data_processing}
            onSave={(updatedSettings) => handleSaveSettings('data_processing', updatedSettings)}
            saving={saving}
          />
        );
      default:
        return <div> .</div>;
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <LoadingSpinner size="large" text="  ..." />
      </div>
    );
  }

  if (error) {
    return (
      <ErrorMessage 
        error={error} 
        onRetry={loadSettings}
        showRetry={true}
      />
    );
  }

  return (
    <div className="space-y-6">
      {/*  */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2"> </h1>
        <p className="text-gray-600">
                .
        </p>
      </div>

      {/*   */}
      {saveMessage && (
        <div className={`p-4 rounded-lg ${
          saveMessage.type === 'success' 
            ? 'bg-green-50 border border-green-200 text-green-800' 
            : 'bg-red-50 border border-red-200 text-red-800'
        }`}>
          {saveMessage.message}
        </div>
      )}

      <div className="flex flex-col lg:flex-row gap-6">
        {/*  */}
        <div className="lg:w-64">
          <div className="bg-white rounded-lg shadow border p-4">
            <h3 className="text-lg font-semibold text-gray-900 mb-4"> </h3>
            <nav className="space-y-2">
              {categories.map((category) => (
                <button
                  key={category.key}
                  onClick={() => setActiveCategory(category.key)}
                  className={`w-full text-left px-3 py-2 rounded-lg transition-colors ${
                    activeCategory === category.key
                      ? 'bg-blue-50 text-blue-700 border border-blue-200'
                      : 'text-gray-700 hover:bg-gray-50'
                  }`}
                >
                  <div className="flex items-center">
                    <span className="text-lg mr-2">{category.icon}</span>
                    <span className="font-medium">{category.label}</span>
                  </div>
                  <div className="text-sm text-gray-500 mt-1">
                    {SettingService.getCategoryDescription(category.key)}
                  </div>
                </button>
              ))}
            </nav>
          </div>

          {/*    */}
          <div className="bg-white rounded-lg shadow border p-4 mt-4">
            <h3 className="text-lg font-semibold text-gray-900 mb-4"> </h3>
            <div className="space-y-2">
              <button
                onClick={handleExportSettings}
                className="w-full btn-secondary text-sm"
              >
                  
              </button>
              <button
                onClick={() => handleResetSettings()}
                className="w-full btn-danger text-sm"
              >
                   
              </button>
            </div>
          </div>
        </div>

        {/*   */}
        <div className="flex-1">
          <div className="bg-white rounded-lg shadow border">
            <div className="border-b border-gray-200 px-6 py-4">
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-xl font-semibold text-gray-900">
                    {categories.find(c => c.key === activeCategory)?.icon} {categories.find(c => c.key === activeCategory)?.label} 
                  </h2>
                  <p className="text-gray-600 mt-1">
                    {SettingService.getCategoryDescription(activeCategory)}
                  </p>
                </div>
                <button
                  onClick={() => handleResetSettings(activeCategory)}
                  className="btn-outline text-sm"
                >
                  
                </button>
              </div>
            </div>
            <div className="p-6">
              {renderSettingsContent()}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Settings; 