import React, { useState } from 'react';
import { SystemSettings } from '../../services/settingService';

interface SecuritySettingsProps {
  settings: SystemSettings['security'];
  onSave: (settings: SystemSettings['security']) => void;
  saving: boolean;
}

const SecuritySettings: React.FC<SecuritySettingsProps> = ({ settings, onSave, saving }) => {
  const [formData, setFormData] = useState(settings);
  const [newIp, setNewIp] = useState('');

  const handleInputChange = (field: keyof SystemSettings['security'], value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleAddIp = () => {
    if (newIp && !formData.ip_whitelist.includes(newIp)) {
      setFormData(prev => ({
        ...prev,
        ip_whitelist: [...prev.ip_whitelist, newIp]
      }));
      setNewIp('');
    }
  };

  const handleRemoveIp = (ip: string) => {
    setFormData(prev => ({
      ...prev,
      ip_whitelist: prev.ip_whitelist.filter(item => item !== ip)
    }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(formData);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/*   */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
              () *
          </label>
          <input
            type="number"
            value={formData.session_timeout}
            onChange={(e) => handleInputChange('session_timeout', parseInt(e.target.value))}
            className="form-input w-full"
            min="300"
            max="86400"
            required
          />
          <p className="text-sm text-gray-500 mt-1">
            300(5) 86400(24)  
          </p>
        </div>

        {/*     */}
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
                *
          </label>
          <input
            type="number"
            value={formData.max_login_attempts}
            onChange={(e) => handleInputChange('max_login_attempts', parseInt(e.target.value))}
            className="form-input w-full"
            min="1"
            max="10"
            required
          />
          <p className="text-sm text-gray-500 mt-1">
            1 10  
          </p>
        </div>
      </div>

      {/*   */}
      <div className="space-y-4">
        <div className="flex items-center">
          <input
            type="checkbox"
            id="require_2fa"
            checked={formData.require_2fa}
            onChange={(e) => handleInputChange('require_2fa', e.target.checked)}
            className="form-checkbox h-4 w-4 text-blue-600"
          />
          <label htmlFor="require_2fa" className="ml-2 text-sm text-gray-700">
            2  
          </label>
          <p className="text-sm text-gray-500 ml-2">
            -   2  
          </p>
        </div>

        <div className="flex items-center">
          <input
            type="checkbox"
            id="audit_logging"
            checked={formData.audit_logging}
            onChange={(e) => handleInputChange('audit_logging', e.target.checked)}
            className="form-checkbox h-4 w-4 text-blue-600"
          />
          <label htmlFor="audit_logging" className="ml-2 text-sm text-gray-700">
              
          </label>
          <p className="text-sm text-gray-500 ml-2">
            -     
          </p>
        </div>
      </div>

      {/* IP  */}
      <div className="space-y-4">
        <h4 className="text-lg font-medium text-gray-900">IP </h4>
        <p className="text-sm text-gray-600">
           IP   .   IP  .
        </p>
        
        <div className="flex gap-2">
          <input
            type="text"
            value={newIp}
            onChange={(e) => setNewIp(e.target.value)}
            className="form-input flex-1"
            placeholder="IP  (: 192.168.1.1)"
          />
          <button
            type="button"
            onClick={handleAddIp}
            className="btn-secondary"
          >
            
          </button>
        </div>

        {formData.ip_whitelist.length > 0 && (
          <div className="space-y-2">
            {formData.ip_whitelist.map((ip, index) => (
              <div key={index} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                <span className="text-sm text-gray-700">{ip}</span>
                <button
                  type="button"
                  onClick={() => handleRemoveIp(ip)}
                  className="text-red-600 hover:text-red-800 text-sm"
                >
                  
                </button>
              </div>
            ))}
          </div>
        )}
      </div>

      {/*   */}
      <div className="flex justify-end pt-6 border-t border-gray-200">
        <button
          type="submit"
          disabled={saving}
          className="btn-primary"
        >
          {saving ? (
            <>
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
               ...
            </>
          ) : (
            ' '
          )}
        </button>
      </div>
    </form>
  );
};

export default SecuritySettings; 