const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:3001/api';

export interface ApiResponse<T = any> {
  success: boolean;
  data?: T;
  error?: string;
  message?: string;
  timestamp: string;
}

interface RequestOptions extends RequestInit {
  timeout?: number;
  retries?: number;
  retryDelay?: number;
}

class ApiService {
  private baseURL: string;
  private defaultTimeout: number = 10000; // 10
  private defaultRetries: number = 3;
  private defaultRetryDelay: number = 1000; // 1

  constructor(baseURL: string) {
    this.baseURL = baseURL;
  }

  private createTimeoutPromise(timeout: number): Promise<never> {
    return new Promise((_, reject) => {
      setTimeout(() => {
        reject(new Error(`Request timeout (${timeout}ms)`));
      }, timeout);
    });
  }

  private async requestWithRetry<T>(
    url: string,
    options: RequestOptions = {},
    retryCount: number = 0
  ): Promise<ApiResponse<T>> {
    const timeout = options.timeout || this.defaultTimeout;
    const maxRetries = options.retries || this.defaultRetries;
    const retryDelay = options.retryDelay || this.defaultRetryDelay;

    const defaultOptions: RequestInit = {
      headers: {
        'Content-Type': 'application/json',
        ...options.headers,
      },
      ...options,
    };

    try {
      //    
      const response = await Promise.race([
        fetch(url, defaultOptions),
        this.createTimeoutPromise(timeout)
      ]);

      const data = await response.json();
      
      if (!response.ok) {
        const errorMessage = data.error || data.message || `HTTP ${response.status}: ${response.statusText}`;
        throw new Error(errorMessage);
      }
      
      return data;
    } catch (error) {
      console.error(`API   ( ${retryCount + 1}/${maxRetries + 1}):`, error);
      
      //    
      const isRetryableError = this.isRetryableError(error);
      
      if (retryCount < maxRetries && isRetryableError) {
        console.log(`${retryDelay}ms  ...`);
        await new Promise(resolve => setTimeout(resolve, retryDelay));
        return this.requestWithRetry<T>(url, options, retryCount + 1);
      }
      
      //      
      throw new Error(
        error instanceof Error 
          ? error.message 
          : 'Unknown error occurred.'
      );
    }
  }

  private isRetryableError(error: any): boolean {
    //  , , 5xx    
    if (error.name === 'TypeError' && error.message.includes('fetch')) {
      return true; //  
    }
    
    if (error.message.includes(' ')) {
      return true; // 
    }
    
    // HTTP   5xx   
    if (error.message.includes('HTTP 5')) {
      return true;
    }
    
    return false;
  }

  private async request<T>(
    endpoint: string,
    options: RequestOptions = {}
  ): Promise<ApiResponse<T>> {
    const url = `${this.baseURL}${endpoint}`;
    return this.requestWithRetry<T>(url, options);
  }

  // GET 
  async get<T>(endpoint: string, params?: Record<string, string>, options?: RequestOptions): Promise<ApiResponse<T>> {
    const url = params 
      ? `${endpoint}?${new URLSearchParams(params).toString()}`
      : endpoint;
    
    return this.request<T>(url, { method: 'GET', ...options });
  }

  // POST 
  async post<T>(endpoint: string, data?: any, options?: RequestOptions): Promise<ApiResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'POST',
      body: data ? JSON.stringify(data) : undefined,
      ...options,
    });
  }

  // PUT 
  async put<T>(endpoint: string, data?: any, options?: RequestOptions): Promise<ApiResponse<T>> {
    return this.request<T>(endpoint, {
      method: 'PUT',
      body: data ? JSON.stringify(data) : undefined,
      ...options,
    });
  }

  // DELETE 
  async delete<T>(endpoint: string, options?: RequestOptions): Promise<ApiResponse<T>> {
    return this.request<T>(endpoint, { method: 'DELETE', ...options });
  }

  // 
  async healthCheck(): Promise<boolean> {
    try {
      const response = await this.get('/health', {}, { timeout: 5000, retries: 1 });
      return response.success;
    } catch (error) {
      console.error(' :', error);
      return false;
    }
  }
}

export const apiService = new ApiService(API_BASE_URL); 