import { apiService, ApiResponse } from './api';

export interface CustomerPersona {
  age_range: string;
  gender: string;
  characteristics: string;
  interests: string;
  pain_points: string;
  goals: string;
  behavior_patterns: string;
}

export interface Scenario {
  id: number;
  name: string;
  description: string;
  business_goal: string;
  target_audience: string;
  customer_persona?: CustomerPersona; //   
  created_at: string;
  is_active: boolean;
}

export interface CreateScenarioData {
  name: string;
  description: string;
  business_goal: string;
  target_audience: string;
  customer_persona?: CustomerPersona; //   
}

export interface UpdateScenarioData extends Partial<CreateScenarioData> {
  is_active?: boolean;
}

export interface ScenarioPerformance {
  total_users: number;
  conversion_rate: number;
  average_session_duration: number;
  revenue_per_user: number;
}

export interface FunnelStage {
  stage_name: string;
  stage_order: number;
  users_reached: number;
  conversion_rate: number;
}

export class ScenarioService {
  //   
  static async getAll(activeOnly: boolean = true): Promise<ApiResponse<Scenario[]>> {
    return apiService.get<Scenario[]>('/scenarios', { active_only: activeOnly.toString() });
  }

  // ID  
  static async getById(id: number): Promise<ApiResponse<Scenario>> {
    return apiService.get<Scenario>(`/scenarios/${id}`);
  }

  //     
  static async getWithStages(id: number): Promise<ApiResponse<Scenario & { stages: FunnelStage[] }>> {
    return apiService.get<Scenario & { stages: FunnelStage[] }>(`/scenarios/${id}/with-stages`);
  }

  //   
  static async create(data: CreateScenarioData): Promise<ApiResponse<Scenario>> {
    return apiService.post<Scenario>('/scenarios', data);
  }

  //  
  static async update(id: number, data: UpdateScenarioData): Promise<ApiResponse<Scenario>> {
    return apiService.put<Scenario>(`/scenarios/${id}`, data);
  }

  //  
  static async delete(id: number): Promise<ApiResponse<void>> {
    return apiService.delete<void>(`/scenarios/${id}`);
  }

  //    
  static async getPerformanceStats(
    id: number, 
    startDate?: string, 
    endDate?: string
  ): Promise<ApiResponse<ScenarioPerformance>> {
    const params: Record<string, string> = {};
    if (startDate) params.start_date = startDate;
    if (endDate) params.end_date = endDate;
    
    return apiService.get<ScenarioPerformance>(
      `/scenarios/${id}/performance`, 
      Object.keys(params).length > 0 ? params : undefined
    );
  }

  //    
  static async getFunnelData(
    id: number, 
    startDate?: string, 
    endDate?: string
  ): Promise<ApiResponse<FunnelStage[]>> {
    const params: Record<string, string> = {};
    if (startDate) params.start_date = startDate;
    if (endDate) params.end_date = endDate;
    
    return apiService.get<FunnelStage[]>(
      `/scenarios/${id}/funnel`, 
      Object.keys(params).length > 0 ? params : undefined
    );
  }

  //    
  static async getDropoutAnalysis(
    id: number, 
    startDate?: string, 
    endDate?: string
  ): Promise<ApiResponse<any>> {
    const params: Record<string, string> = {};
    if (startDate) params.start_date = startDate;
    if (endDate) params.end_date = endDate;
    
    return apiService.get<any>(
      `/scenarios/${id}/dropout`, 
      Object.keys(params).length > 0 ? params : undefined
    );
  }

  //    
  static createDefaultPersona(): CustomerPersona {
    return {
      age_range: '',
      gender: '',
      characteristics: '',
      interests: '',
      pain_points: '',
      goals: '',
      behavior_patterns: ''
    };
  }

  //     ( )
  static addDefaultPersonaToScenario(scenario: any): Scenario {
    return {
      ...scenario,
      customer_persona: scenario.customer_persona || this.createDefaultPersona()
    };
  }
} 