import React from 'react';
import { CategoryMetrics, CategoryType } from '../services/dashboardService';

interface CategoryMetricsProps {
  category: CategoryType;
  metrics: CategoryMetrics | Partial<CategoryMetrics>;
}

const CategoryMetricsComponent: React.FC<CategoryMetricsProps> = ({ category, metrics }) => {
  const renderEcommerceMetrics = (ecommerce: CategoryMetrics['ecommerce']) => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-green-600">{(ecommerce?.total_revenue || 0).toLocaleString()}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">  </div>
        <div className="text-2xl font-bold text-blue-600">{(ecommerce?.average_order_value || 0).toLocaleString()}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-red-600">{ecommerce?.cart_abandonment_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"></div>
        <div className="text-2xl font-bold text-purple-600">{ecommerce?.conversion_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">  </div>
        <div className="text-2xl font-bold text-indigo-600">{(ecommerce?.customer_lifetime_value || 0).toLocaleString()}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"></div>
        <div className="text-2xl font-bold text-teal-600">{ecommerce?.repeat_purchase_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">-</div>
        <div className="text-2xl font-bold text-orange-600">{ecommerce?.product_view_to_purchase_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-pink-600">{ecommerce?.checkout_completion_rate || 0}%</div>
      </div>
    </div>
  );

  const renderLeadGenerationMetrics = (leadGeneration: CategoryMetrics['lead_generation']) => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">  </div>
        <div className="text-2xl font-bold text-green-600">{(leadGeneration?.total_leads || 0).toLocaleString()}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-blue-600">{leadGeneration?.lead_conversion_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-red-600">{(leadGeneration?.cost_per_lead || 0).toLocaleString()}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">  </div>
        <div className="text-2xl font-bold text-purple-600">{leadGeneration?.lead_quality_score || 0}/10</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-indigo-600">{leadGeneration?.form_completion_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-teal-600">{leadGeneration?.email_open_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"></div>
        <div className="text-2xl font-bold text-orange-600">{leadGeneration?.click_through_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">- </div>
        <div className="text-2xl font-bold text-pink-600">{leadGeneration?.lead_to_customer_rate || 0}%</div>
      </div>
    </div>
  );

  const renderGeneralWebsiteMetrics = (generalWebsite: CategoryMetrics['general_website']) => (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-green-600">{(generalWebsite?.total_page_views || 0).toLocaleString()}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-blue-600">{(generalWebsite?.unique_visitors || 0).toLocaleString()}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"></div>
        <div className="text-2xl font-bold text-red-600">{generalWebsite?.bounce_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">  </div>
        <div className="text-2xl font-bold text-purple-600">{generalWebsite?.average_session_duration || 0}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-indigo-600">{generalWebsite?.pages_per_session || 0}</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"></div>
        <div className="text-2xl font-bold text-teal-600">{generalWebsite?.return_visitor_rate || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600"> </div>
        <div className="text-2xl font-bold text-orange-600">{generalWebsite?.mobile_traffic_percentage || 0}%</div>
      </div>
      <div className="bg-white p-4 rounded-lg shadow border">
        <div className="text-sm font-medium text-gray-600">  </div>
        <div className="text-sm text-gray-600 mt-1">
          {(generalWebsite?.top_exit_pages || []).slice(0, 2).map((page, index) => (
            <div key={index} className="truncate">{page}</div>
          ))}
        </div>
      </div>
    </div>
  );

  const renderAllMetrics = () => (
    <div className="space-y-6">
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4">E-commerce </h3>
        {renderEcommerceMetrics(metrics.ecommerce || {
          total_revenue: 0,
          average_order_value: 0,
          cart_abandonment_rate: 0,
          conversion_rate: 0,
          customer_lifetime_value: 0,
          repeat_purchase_rate: 0,
          product_view_to_purchase_rate: 0,
          checkout_completion_rate: 0
        })}
      </div>
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4">  </h3>
        {renderLeadGenerationMetrics(metrics.lead_generation || {
          total_leads: 0,
          lead_conversion_rate: 0,
          cost_per_lead: 0,
          lead_quality_score: 0,
          form_completion_rate: 0,
          email_open_rate: 0,
          click_through_rate: 0,
          lead_to_customer_rate: 0
        })}
      </div>
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4">  </h3>
        {renderGeneralWebsiteMetrics(metrics.general_website || {
          total_page_views: 0,
          unique_visitors: 0,
          bounce_rate: 0,
          average_session_duration: 0,
          pages_per_session: 0,
          return_visitor_rate: 0,
          mobile_traffic_percentage: 0,
          top_exit_pages: []
        })}
      </div>
    </div>
  );

  return (
    <div className="mb-6">
      <h2 className="text-xl font-bold text-gray-900 mb-4">  </h2>
      {category === 'all' && renderAllMetrics()}
      {category === 'ecommerce' && metrics.ecommerce && renderEcommerceMetrics(metrics.ecommerce)}
      {category === 'lead_generation' && metrics.lead_generation && renderLeadGenerationMetrics(metrics.lead_generation)}
      {category === 'general_website' && metrics.general_website && renderGeneralWebsiteMetrics(metrics.general_website)}
    </div>
  );
};

export default CategoryMetricsComponent; 