import React from 'react';
import { 
  UsersIcon, 
  ComputerDesktopIcon, 
  ShoppingCartIcon,
  ChartBarIcon,
  CurrencyDollarIcon,
  DocumentTextIcon,
  EyeIcon,
  UserGroupIcon
} from '@heroicons/react/24/outline';

interface MetricCardProps {
  title: string;
  value: string;
  change?: string;
  changeType?: 'positive' | 'negative' | 'neutral';
  icon?: 'users' | 'sessions' | 'conversions' | 'conversion-rate' | 'revenue' | 'order-value' | 'leads' | 'lead-conversion' | 'page-views' | 'unique-visitors';
}

const MetricCard: React.FC<MetricCardProps> = ({
  title,
  value,
  change,
  changeType = 'neutral',
  icon = 'users'
}) => {
  const getIcon = () => {
    switch (icon) {
      case 'users':
        return <UsersIcon className="h-8 w-8 text-primary-600" />;
      case 'sessions':
        return <ComputerDesktopIcon className="h-8 w-8 text-primary-600" />;
      case 'conversions':
        return <ShoppingCartIcon className="h-8 w-8 text-primary-600" />;
      case 'conversion-rate':
        return <ChartBarIcon className="h-8 w-8 text-primary-600" />;
      case 'revenue':
        return <CurrencyDollarIcon className="h-8 w-8 text-primary-600" />;
      case 'order-value':
        return <CurrencyDollarIcon className="h-8 w-8 text-primary-600" />;
      case 'leads':
        return <DocumentTextIcon className="h-8 w-8 text-primary-600" />;
      case 'lead-conversion':
        return <ChartBarIcon className="h-8 w-8 text-primary-600" />;
      case 'page-views':
        return <EyeIcon className="h-8 w-8 text-primary-600" />;
      case 'unique-visitors':
        return <UserGroupIcon className="h-8 w-8 text-primary-600" />;
      default:
        return <UsersIcon className="h-8 w-8 text-primary-600" />;
    }
  };

  const getChangeColor = () => {
    switch (changeType) {
      case 'positive':
        return 'text-green-600';
      case 'negative':
        return 'text-red-600';
      default:
        return 'text-gray-600';
    }
  };

  const getChangeIcon = () => {
    switch (changeType) {
      case 'positive':
        return (
          <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M3.293 9.707a1 1 0 010-1.414l6-6a1 1 0 011.414 0l6 6a1 1 0 01-1.414 1.414L11 5.414V17a1 1 0 11-2 0V5.414L4.707 9.707a1 1 0 01-1.414 0z" clipRule="evenodd" />
          </svg>
        );
      case 'negative':
        return (
          <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
            <path fillRule="evenodd" d="M16.707 10.293a1 1 0 010 1.414l-6 6a1 1 0 01-1.414 0l-6-6a1 1 0 111.414-1.414L9 14.586V3a1 1 0 012 0v11.586l4.293-4.293a1 1 0 011.414 0z" clipRule="evenodd" />
          </svg>
        );
      default:
        return null;
    }
  };

  return (
    <div className="metric-card">
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center">
          {getIcon()}
          <div className="ml-3">
            <p className="text-sm font-medium text-gray-600">{title}</p>
          </div>
        </div>
        {change && (
          <div className={`flex items-center text-sm font-medium ${getChangeColor()}`}>
            {getChangeIcon()}
            <span className="ml-1">{change}</span>
          </div>
        )}
      </div>
      <div className="metric-value">{value}</div>
    </div>
  );
};

export default MetricCard; 