interface Touchpoint {
  id: string;
  name: string;
  description: string;
  channel: string; // 'digital', 'physical', 'social', 'support'
  priority: 'high' | 'medium' | 'low';
  status: 'active' | 'planned' | 'inactive';
  metrics: {
    reach: number;
    engagement: number;
    conversion: number;
    satisfaction: number;
  };
}

interface Opportunity {
  id: string;
  title: string;
  description: string;
  impact: 'high' | 'medium' | 'low';
  effort: 'high' | 'medium' | 'low';
  roi: number;
  timeline: string;
  status: 'identified' | 'in-progress' | 'completed';
}

interface DataMetric {
  name: string;
  value: number;
  unit: string;
  trend: 'up' | 'down' | 'stable';
  target: number;
  benchmark: number;
}

interface Solution {
  id: string;
  title: string;
  description: string;
  type: 'immediate' | 'short-term' | 'long-term';
  resources: string[];
  expectedOutcome: string;
  successMetrics: string[];
  status: 'proposed' | 'approved' | 'implemented';
}

interface JourneyStage {
  id: string;
  name: string;
  description: string;
  duration: string;
  emotions: number; // -100 to 100
  painPoints: string[];
  touchpoints: Touchpoint[];
  opportunities: Opportunity[];
  dataMetrics: DataMetric[];
  solutions: Solution[];
  overallMetrics: {
    users: number;
    conversionRate: number;
    avgTime: number;
    satisfaction: number;
    dropoffRate: number;
    revenue: number;
  };
}

