import React, { useState } from 'react';

interface NotificationSettingsProps {
  settings: {
    email_notifications: boolean;
    slack_notifications: boolean;
    alert_thresholds: {
      conversion_rate_min: number;
      revenue_target: number;
      lead_quality_min: number;
      bounce_rate_max: number;
    };
    notification_frequency: 'realtime' | 'hourly' | 'daily' | 'weekly';
  };
  onSave: (settings: any) => void;
  saving: boolean;
}

const NotificationSettings: React.FC<NotificationSettingsProps> = ({ settings, onSave, saving }) => {
  const [formData, setFormData] = useState(settings);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(formData);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      <div className="space-y-4">
        <div className="flex items-center">
          <input
            type="checkbox"
            id="email_notifications"
            checked={formData.email_notifications}
            onChange={(e) => setFormData({ ...formData, email_notifications: e.target.checked })}
            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
          <label htmlFor="email_notifications" className="ml-2 block text-sm text-gray-900">
             
          </label>
        </div>

        <div className="flex items-center">
          <input
            type="checkbox"
            id="slack_notifications"
            checked={formData.slack_notifications}
            onChange={(e) => setFormData({ ...formData, slack_notifications: e.target.checked })}
            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
          />
          <label htmlFor="slack_notifications" className="ml-2 block text-sm text-gray-900">
            Slack 
          </label>
        </div>
      </div>

      <div>
        <label className="block text-sm font-medium text-gray-700 mb-2">
           
        </label>
        <select
          value={formData.notification_frequency}
          onChange={(e) => setFormData({ ...formData, notification_frequency: e.target.value as any })}
          className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
        >
          <option value="realtime"></option>
          <option value="hourly"></option>
          <option value="daily"></option>
          <option value="weekly"></option>
        </select>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
              (%)
          </label>
          <input
            type="number"
            value={formData.alert_thresholds.conversion_rate_min}
            onChange={(e) => setFormData({
              ...formData,
              alert_thresholds: {
                ...formData.alert_thresholds,
                conversion_rate_min: parseFloat(e.target.value)
              }
            })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            min="0"
            max="100"
            step="0.1"
            required
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
              ()
          </label>
          <input
            type="number"
            value={formData.alert_thresholds.revenue_target}
            onChange={(e) => setFormData({
              ...formData,
              alert_thresholds: {
                ...formData.alert_thresholds,
                revenue_target: parseInt(e.target.value)
              }
            })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            min="0"
            required
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
               
          </label>
          <input
            type="number"
            value={formData.alert_thresholds.lead_quality_min}
            onChange={(e) => setFormData({
              ...formData,
              alert_thresholds: {
                ...formData.alert_thresholds,
                lead_quality_min: parseFloat(e.target.value)
              }
            })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            min="0"
            max="10"
            step="0.1"
            required
          />
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-2">
              (%)
          </label>
          <input
            type="number"
            value={formData.alert_thresholds.bounce_rate_max}
            onChange={(e) => setFormData({
              ...formData,
              alert_thresholds: {
                ...formData.alert_thresholds,
                bounce_rate_max: parseFloat(e.target.value)
              }
            })}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            min="0"
            max="100"
            step="0.1"
            required
          />
        </div>
      </div>

      <div className="flex justify-end">
        <button
          type="submit"
          disabled={saving}
          className="btn-primary disabled:opacity-50"
        >
          {saving ? ' ...' : ''}
        </button>
      </div>
    </form>
  );
};

export default NotificationSettings; 