import React from 'react';

interface FunnelData {
  stage_name: string;
  stage_order: number;
  users_reached: number;
  conversion_rate: number;
}

interface FunnelChartProps {
  data: FunnelData[];
}

const FunnelChart: React.FC<FunnelChartProps> = ({ data }) => {
  if (!data || data.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
          .
      </div>
    );
  }

  //      
  const maxUsers = Math.max(...data.map(item => item.users_reached));

  return (
    <div className="space-y-4">
      {data.map((stage, index) => {
        const widthPercentage = (stage.users_reached / maxUsers) * 100;
        const isLastStage = index === data.length - 1;
        
        return (
          <div key={stage.stage_order} className="relative">
            {/*   */}
            <div className="flex items-center justify-between mb-2">
              <div className="flex items-center">
                <div className="w-6 h-6 bg-primary-600 text-white text-xs font-bold rounded-full flex items-center justify-center mr-3">
                  {stage.stage_order}
                </div>
                <span className="font-medium text-gray-900">{stage.stage_name}</span>
              </div>
              <div className="text-right">
                <div className="text-lg font-bold text-primary-600">
                  {stage.users_reached.toLocaleString()}
                </div>
                <div className="text-sm text-gray-600">
                  {stage.conversion_rate.toFixed(1)}%
                </div>
              </div>
            </div>

            {/*   */}
            <div className="relative">
              <div className="w-full bg-gray-200 rounded-full h-8">
                <div
                  className="bg-gradient-to-r from-primary-500 to-primary-600 h-8 rounded-full transition-all duration-500 ease-out"
                  style={{ width: `${widthPercentage}%` }}
                />
              </div>
              
              {/*   */}
              {!isLastStage && (
                <div className="absolute -bottom-6 left-0 right-0 text-center">
                  <div className="text-xs text-gray-500">
                    {stage.users_reached > 0 && data[index + 1] && (
                      <>
                        {((stage.users_reached - data[index + 1].users_reached) / stage.users_reached * 100).toFixed(1)}% 
                      </>
                    )}
                  </div>
                </div>
              )}
            </div>
          </div>
        );
      })}

      {/*   */}
      <div className="mt-8 p-4 bg-gray-50 rounded-lg">
        <h4 className="font-medium text-gray-900 mb-2"> </h4>
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <span className="text-gray-600"> :</span>
            <span className="ml-2 font-medium">{data[0]?.users_reached.toLocaleString()}</span>
          </div>
          <div>
            <span className="text-gray-600"> :</span>
            <span className="ml-2 font-medium">{data[data.length - 1]?.users_reached.toLocaleString()}</span>
          </div>
          <div>
            <span className="text-gray-600"> :</span>
            <span className="ml-2 font-medium">
              {data[0] && data[data.length - 1] 
                ? ((data[data.length - 1].users_reached / data[0].users_reached) * 100).toFixed(1)
                : 0}%
            </span>
          </div>
          <div>
            <span className="text-gray-600"> :</span>
            <span className="ml-2 font-medium">
              {data[0] && data[data.length - 1] 
                ? (data[0].users_reached - data[data.length - 1].users_reached).toLocaleString()
                : 0}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FunnelChart; 