import React from 'react';

interface EventData {
  user_id: string;
  action_type: string;
  performed_at: string;
}

interface RecentEventsTableProps {
  events: EventData[];
}

const RecentEventsTable: React.FC<RecentEventsTableProps> = ({ events }) => {
  if (!events || events.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
          .
      </div>
    );
  }

  const getActionTypeLabel = (actionType: string) => {
    switch (actionType) {
      case 'page_view':
        return ' ';
      case 'purchase':
        return '';
      case 'signup':
        return '';
      case 'login':
        return '';
      case 'add_to_cart':
        return ' ';
      case 'search':
        return '';
      default:
        return actionType;
    }
  };

  const getActionTypeColor = (actionType: string) => {
    switch (actionType) {
      case 'purchase':
        return 'bg-green-100 text-green-800';
      case 'signup':
        return 'bg-blue-100 text-blue-800';
      case 'login':
        return 'bg-purple-100 text-purple-800';
      case 'add_to_cart':
        return 'bg-orange-100 text-orange-800';
      case 'search':
        return 'bg-indigo-100 text-indigo-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffInMinutes = Math.floor((now.getTime() - date.getTime()) / (1000 * 60));
    
    if (diffInMinutes < 1) {
      return ' ';
    } else if (diffInMinutes < 60) {
      return `${diffInMinutes} `;
    } else if (diffInMinutes < 1440) {
      const hours = Math.floor(diffInMinutes / 60);
      return `${hours} `;
    } else {
      return date.toLocaleDateString('ko-KR', {
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    }
  };

  return (
    <div className="overflow-hidden">
      <div className="overflow-x-auto">
        <table className="data-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {events.map((event, index) => (
              <tr key={index} className="hover:bg-gray-50 transition-colors duration-150">
                <td>
                  <div className="flex items-center">
                    <div className="w-8 h-8 bg-primary-100 rounded-full flex items-center justify-center">
                      <span className="text-xs font-medium text-primary-600">
                        {event.user_id.slice(-2).toUpperCase()}
                      </span>
                    </div>
                    <span className="ml-3 text-sm font-medium text-gray-900">
                      {event.user_id}
                    </span>
                  </div>
                </td>
                <td>
                  <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getActionTypeColor(event.action_type)}`}>
                    {getActionTypeLabel(event.action_type)}
                  </span>
                </td>
                <td>
                  <span className="text-sm text-gray-600">
                    {formatTime(event.performed_at)}
                  </span>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/*   */}
      <div className="mt-4 p-4 bg-gray-50 rounded-lg">
        <h4 className="font-medium text-gray-900 mb-3"> </h4>
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <span className="text-gray-600"> :</span>
            <span className="ml-2 font-medium">{events.length}</span>
          </div>
          <div>
            <span className="text-gray-600"> :</span>
            <span className="ml-2 font-medium">
              {new Set(events.map(event => event.user_id)).size}
            </span>
          </div>
          <div>
            <span className="text-gray-600">  :</span>
            <span className="ml-2 font-medium">
              {(() => {
                const actionCounts = events.reduce((acc, event) => {
                  acc[event.action_type] = (acc[event.action_type] || 0) + 1;
                  return acc;
                }, {} as Record<string, number>);
                
                const mostActive = Object.entries(actionCounts)
                  .sort(([,a], [,b]) => b - a)[0];
                
                return mostActive ? getActionTypeLabel(mostActive[0]) : '';
              })()}
            </span>
          </div>
          <div>
            <span className="text-gray-600"> :</span>
            <span className="ml-2 font-medium">
              {(() => {
                const now = new Date();
                const avgTime = events.reduce((sum, event) => {
                  return sum + (now.getTime() - new Date(event.performed_at).getTime());
                }, 0) / events.length;
                
                const avgMinutes = Math.floor(avgTime / (1000 * 60));
                if (avgMinutes < 60) {
                  return `${avgMinutes}`;
                } else {
                  const hours = Math.floor(avgMinutes / 60);
                  return `${hours}`;
                }
              })()}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RecentEventsTable; 