import { apiService, ApiResponse } from './api';

export interface SystemSettings {
  general: {
    company_name: string;
    timezone: string;
    date_format: string;
    currency: string;
    language: string;
    data_retention_days: number;
    auto_refresh_interval: number;
  };
  dashboard: {
    default_category: 'all' | 'ecommerce' | 'lead_generation' | 'general_website';
    chart_theme: 'light' | 'dark' | 'auto';
    show_real_time_data: boolean;
    max_data_points: number;
    enable_export: boolean;
    enable_sharing: boolean;
  };
  notifications: {
    email_notifications: boolean;
    slack_notifications: boolean;
    alert_thresholds: {
      conversion_rate_min: number;
      revenue_target: number;
      lead_quality_min: number;
      bounce_rate_max: number;
    };
    notification_frequency: 'realtime' | 'hourly' | 'daily' | 'weekly';
  };
  integrations: {
    google_analytics: {
      enabled: boolean;
      tracking_id: string;
      custom_dimensions: string[];
    };
    facebook_pixel: {
      enabled: boolean;
      pixel_id: string;
    };
    google_ads: {
      enabled: boolean;
      conversion_tracking: boolean;
    };
    crm_system: {
      enabled: boolean;
      system_type: 'salesforce' | 'hubspot' | 'pipedrive' | 'custom';
      api_endpoint: string;
      api_key: string;
    };
  };
  security: {
    session_timeout: number;
    max_login_attempts: number;
    require_2fa: boolean;
    ip_whitelist: string[];
    audit_logging: boolean;
  };
  data_processing: {
    real_time_processing: boolean;
    batch_processing_interval: number;
    data_aggregation_rules: {
      hourly: boolean;
      daily: boolean;
      weekly: boolean;
      monthly: boolean;
    };
    data_cleanup_schedule: string;
  };
}

export type SettingCategory = keyof SystemSettings;

export class SettingService {
  //   
  static async getAllSettings(): Promise<ApiResponse<SystemSettings>> {
    return apiService.get<SystemSettings>('/settings');
  }

  //    
  static async getSettingsByCategory(category: SettingCategory): Promise<ApiResponse<Partial<SystemSettings>>> {
    return apiService.get<Partial<SystemSettings>>(`/settings/category/${category}`);
  }

  //  
  static async updateSettings(category: SettingCategory, settings: Partial<SystemSettings[SettingCategory]>): Promise<ApiResponse<Partial<SystemSettings>>> {
    return apiService.put<Partial<SystemSettings>>('/settings/update', {
      category,
      settings
    });
  }

  //  
  static async resetSettings(category?: SettingCategory): Promise<ApiResponse<SystemSettings | Partial<SystemSettings>>> {
    const url = category ? `/settings/reset/${category}` : '/settings/reset';
    return apiService.post<SystemSettings | Partial<SystemSettings>>(url);
  }

  //  
  static async exportSettings(format: 'json' | 'yaml' = 'json'): Promise<Blob> {
    const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:3001'}/api/settings/export?format=${format}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });

    if (!response.ok) {
      throw new Error('  .');
    }

    return response.blob();
  }

  //   ( )
  static async importSettings(file: File): Promise<ApiResponse<SystemSettings>> {
    const formData = new FormData();
    formData.append('settings', file);

    const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:3001'}/api/settings/import`, {
      method: 'POST',
      body: formData,
    });

    if (!response.ok) {
      throw new Error('  .');
    }

    return response.json();
  }

  //   
  static validateSettings(category: SettingCategory, settings: any): { isValid: boolean; errors: string[] } {
    const errors: string[] = [];

    switch (category) {
      case 'general':
        if (settings.company_name && settings.company_name.length < 2) {
          errors.push(' 2  .');
        }
        if (settings.data_retention_days && (settings.data_retention_days < 1 || settings.data_retention_days > 3650)) {
          errors.push('   1 3650  .');
        }
        if (settings.auto_refresh_interval && (settings.auto_refresh_interval < 5 || settings.auto_refresh_interval > 3600)) {
          errors.push('   5 3600  .');
        }
        break;

      case 'dashboard':
        if (settings.max_data_points && (settings.max_data_points < 100 || settings.max_data_points > 10000)) {
          errors.push('   100 10000  .');
        }
        break;

      case 'notifications':
        if (settings.alert_thresholds) {
          if (settings.alert_thresholds.conversion_rate_min < 0 || settings.alert_thresholds.conversion_rate_min > 100) {
            errors.push('  0 100  .');
          }
          if (settings.alert_thresholds.bounce_rate_max < 0 || settings.alert_thresholds.bounce_rate_max > 100) {
            errors.push('  0 100  .');
          }
          if (settings.alert_thresholds.revenue_target < 0) {
            errors.push('  0  .');
          }
          if (settings.alert_thresholds.lead_quality_min < 0 || settings.alert_thresholds.lead_quality_min > 10) {
            errors.push('   0 10  .');
          }
        }
        break;

      case 'security':
        if (settings.session_timeout && (settings.session_timeout < 300 || settings.session_timeout > 86400)) {
          errors.push('  300 86400  .');
        }
        if (settings.max_login_attempts && (settings.max_login_attempts < 1 || settings.max_login_attempts > 10)) {
          errors.push('    1 10  .');
        }
        break;

      case 'integrations':
        if (settings.google_analytics?.enabled && !settings.google_analytics.tracking_id) {
          errors.push('Google Analytics    ID .');
        }
        if (settings.facebook_pixel?.enabled && !settings.facebook_pixel.pixel_id) {
          errors.push('Facebook Pixel   Pixel ID .');
        }
        if (settings.crm_system?.enabled) {
          if (!settings.crm_system.api_endpoint) {
            errors.push('CRM    API  .');
          }
          if (!settings.crm_system.api_key) {
            errors.push('CRM    API  .');
          }
        }
        break;

      case 'data_processing':
        if (settings.batch_processing_interval && (settings.batch_processing_interval < 60 || settings.batch_processing_interval > 86400)) {
          errors.push('   60 86400  .');
        }
        break;
    }

    return {
      isValid: errors.length === 0,
      errors
    };
  }

  //   
  static getCategoryDescription(category: SettingCategory): string {
    const descriptions = {
      general: '   (, ,  )',
      dashboard: '    ',
      notifications: '   ',
      integrations: '   ',
      security: '   ',
      data_processing: '    '
    };
    return descriptions[category];
  }

  //   
  static getCategoryIcon(category: SettingCategory): string {
    const icons = {
      general: '',
      dashboard: '',
      notifications: '',
      integrations: '',
      security: '',
      data_processing: ''
    };
    return icons[category];
  }
} 