import React, { useState, useEffect } from 'react';
import { ScenarioService, Scenario } from '../services/scenarioService';
import { DashboardService } from '../services/dashboardService';

interface AdminPanelProps {
  onDataUpdate?: () => void;
}

const AdminPanel: React.FC<AdminPanelProps> = ({ onDataUpdate }) => {
  const [scenarios, setScenarios] = useState<Scenario[]>([]);
  const [selectedScenario, setSelectedScenario] = useState<number | null>(null);
  const [showEventForm, setShowEventForm] = useState(false);
  const [eventData, setEventData] = useState({
    user_id: '',
    action_type: '',
    scenario_id: '',
    metadata: ''
  });

  useEffect(() => {
    fetchScenarios();
  }, []);

  const fetchScenarios = async () => {
    try {
      const response = await ScenarioService.getAll(true);
      if (response.success) {
        setScenarios(response.data || []);
      }
    } catch (error) {
      console.error('  :', error);
    }
  };

  const handleEventSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      //    API 
      const response = await fetch('http://localhost:3001/api/events', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          user_id: eventData.user_id,
          action_type: eventData.action_type,
          scenario_id: selectedScenario,
          metadata: eventData.metadata,
          performed_at: new Date().toISOString()
        }),
      });

      if (response.ok) {
        alert('  .');
        setEventData({ user_id: '', action_type: '', scenario_id: '', metadata: '' });
        setShowEventForm(false);
        
        //     
        if (onDataUpdate) {
          onDataUpdate();
        }
      } else {
        throw new Error('  .');
      }
    } catch (error) {
      alert('    .');
      console.error('Event submission error:', error);
    }
  };

  const generateMockData = async () => {
    try {
      //      
      const mockEvents = [
        { user_id: 'user_001', action_type: 'page_view', scenario_id: selectedScenario || 1 },
        { user_id: 'user_002', action_type: 'purchase', scenario_id: selectedScenario || 1 },
        { user_id: 'user_003', action_type: 'signup', scenario_id: selectedScenario || 1 },
        { user_id: 'user_004', action_type: 'cart_add', scenario_id: selectedScenario || 1 },
        { user_id: 'user_005', action_type: 'checkout_start', scenario_id: selectedScenario || 1 }
      ];

      //    
      await Promise.all(
        mockEvents.map(event =>
          fetch('http://localhost:3001/api/events', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              ...event,
              performed_at: new Date().toISOString()
            }),
          })
        )
      );

      alert('   .');
      
      //     
      if (onDataUpdate) {
        onDataUpdate();
      }
    } catch (error) {
      alert('     .');
      console.error('Mock data generation error:', error);
    }
  };

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      <h2 className="text-2xl font-bold text-gray-900 mb-6"> </h2>
      
      {/*   */}
      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-2">
           
        </label>
        <select
          value={selectedScenario || ''}
          onChange={(e) => setSelectedScenario(Number(e.target.value) || null)}
          className="form-input w-full"
        >
          <option value=""> </option>
          {scenarios.map((scenario) => (
            <option key={scenario.id} value={scenario.id}>
              {scenario.name}
            </option>
          ))}
        </select>
      </div>

      {/*    */}
      <div className="mb-6">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold text-gray-900"> </h3>
          <button
            onClick={() => setShowEventForm(!showEventForm)}
            className="btn-secondary"
          >
            {showEventForm ? ' ' : ' '}
          </button>
        </div>

        {showEventForm && (
          <form onSubmit={handleEventSubmit} className="space-y-4 p-4 border rounded-lg">
            <div>
              <label className="form-label"> ID</label>
              <input
                type="text"
                value={eventData.user_id}
                onChange={(e) => setEventData({...eventData, user_id: e.target.value})}
                className="form-input"
                required
              />
            </div>

            <div>
              <label className="form-label"> </label>
              <select
                value={eventData.action_type}
                onChange={(e) => setEventData({...eventData, action_type: e.target.value})}
                className="form-input"
                required
              >
                <option value=""> </option>
                <option value="page_view"> </option>
                <option value="purchase"></option>
                <option value="signup"></option>
                <option value="cart_add"> </option>
                <option value="checkout_start"> </option>
                <option value="checkout_complete"> </option>
              </select>
            </div>

            <div>
              <label className="form-label"> ()</label>
              <textarea
                value={eventData.metadata}
                onChange={(e) => setEventData({...eventData, metadata: e.target.value})}
                className="form-input"
                rows={2}
                placeholder="  JSON  "
              />
            </div>

            <button type="submit" className="btn-primary w-full">
               
            </button>
          </form>
        )}
      </div>

      {/*    */}
      <div className="mb-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">  </h3>
        <div className="space-y-2">
          <p className="text-sm text-gray-600">
                  .
          </p>
          <button
            onClick={generateMockData}
            className="btn-primary"
            disabled={!selectedScenario}
          >
              
          </button>
        </div>
      </div>

      {/*   */}
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4"> </h3>
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div className="bg-green-50 p-3 rounded">
            <div className="font-medium text-green-800">API </div>
            <div className="text-green-600"></div>
          </div>
          <div className="bg-blue-50 p-3 rounded">
            <div className="font-medium text-blue-800"></div>
            <div className="text-blue-600"></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminPanel; 