import React, { useState, useEffect } from 'react';
import { ScenarioService, Scenario, CreateScenarioData, UpdateScenarioData, CustomerPersona } from '../services/scenarioService';
import LoadingSpinner from './LoadingSpinner';
import ErrorMessage from './ErrorMessage';

const ScenarioManager: React.FC = () => {
  const [scenarios, setScenarios] = useState<Scenario[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [showEditForm, setShowEditForm] = useState(false);
  const [editingScenario, setEditingScenario] = useState<Scenario | null>(null);
  const [formData, setFormData] = useState<CreateScenarioData>({
    name: '',
    description: '',
    business_goal: '',
    target_audience: '',
    customer_persona: ScenarioService.createDefaultPersona()
  });

  useEffect(() => {
    fetchScenarios();
  }, []);

  const fetchScenarios = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await ScenarioService.getAll(true);
      
      if (!response.success) {
        throw new Error(response.error || '   .');
      }
      
      setScenarios(response.data || []);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : '     .';
      setError(errorMessage);
      console.error('Scenario fetch error:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleCreateScenario = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      const response = await ScenarioService.create(formData);
      
      if (!response.success) {
        throw new Error(response.error || '  .');
      }
      
      setScenarios([response.data!, ...scenarios]);
      
      //  
      setFormData({
        name: '',
        description: '',
        business_goal: '',
        target_audience: '',
        customer_persona: ScenarioService.createDefaultPersona()
      });
      setShowCreateForm(false);
      
      alert('  .');
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : '    .';
      alert(errorMessage);
      console.error('Scenario creation error:', err);
    }
  };

  const handleEditScenario = (scenario: Scenario) => {
    setEditingScenario(scenario);
    setFormData({
      name: scenario.name,
      description: scenario.description,
      business_goal: scenario.business_goal,
      target_audience: scenario.target_audience,
      customer_persona: scenario.customer_persona || ScenarioService.createDefaultPersona()
    });
    setShowEditForm(true);
  };

  const handleUpdateScenario = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!editingScenario) return;
    
    try {
      const updateData: UpdateScenarioData = {
        name: formData.name,
        description: formData.description,
        business_goal: formData.business_goal,
        target_audience: formData.target_audience,
        customer_persona: formData.customer_persona
      };

      const response = await ScenarioService.update(editingScenario.id, updateData);
      
      if (!response.success) {
        throw new Error(response.error || '  .');
      }
      
      setScenarios(scenarios.map(scenario => 
        scenario.id === editingScenario.id ? response.data! : scenario
      ));
      
      //  
      setFormData({
        name: '',
        description: '',
        business_goal: '',
        target_audience: '',
        customer_persona: ScenarioService.createDefaultPersona()
      });
      setShowEditForm(false);
      setEditingScenario(null);
      
      alert('  .');
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : '    .';
      alert(errorMessage);
      console.error('Scenario update error:', err);
    }
  };

  const handleDeleteScenario = async (id: number) => {
    if (!window.confirm('   ?')) {
      return;
    }
    
    try {
      const response = await ScenarioService.delete(id);
      
      if (!response.success) {
        throw new Error(response.error || '  .');
      }
      
      //    
      setScenarios(scenarios.filter(scenario => scenario.id !== id));
      
      alert('  .');
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : '    .';
      alert(errorMessage);
      console.error('Scenario deletion error:', err);
    }
  };

  const handlePersonaChange = (field: keyof CustomerPersona, value: string) => {
    setFormData(prev => ({
      ...prev,
      customer_persona: {
        ...prev.customer_persona!,
        [field]: value
      }
    }));
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <LoadingSpinner size="large" text="  ..." />
      </div>
    );
  }

  if (error) {
    return (
      <ErrorMessage 
        error={error} 
        onRetry={fetchScenarios}
        showRetry={true}
      />
    );
  }

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2"> </h1>
          <p className="text-gray-600">
                
          </p>
        </div>
        <button
          onClick={() => setShowCreateForm(true)}
          className="btn-primary"
        >
            
        </button>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {scenarios.filter(s => s.is_active).map((scenario) => (
          <div key={scenario.id} className="dashboard-card">
            <div className="flex justify-between items-start mb-4">
              <h3 className="text-lg font-semibold text-gray-900">{scenario.name}</h3>
              <div className="flex space-x-2">
                <button 
                  onClick={() => handleEditScenario(scenario)}
                  className="text-gray-400 hover:text-blue-600"
                  title=""
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                  </svg>
                </button>
                <button 
                  onClick={() => handleDeleteScenario(scenario.id)}
                  className="text-gray-400 hover:text-red-600"
                  title=""
                >
                  <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                  </svg>
                </button>
              </div>
            </div>
            
            <p className="text-gray-600 mb-4">{scenario.description}</p>
            
            <div className="space-y-2 mb-4">
              <div>
                <span className="text-sm font-medium text-gray-700"> :</span>
                <p className="text-sm text-gray-600">{scenario.business_goal}</p>
              </div>
              <div>
                <span className="text-sm font-medium text-gray-700"> :</span>
                <p className="text-sm text-gray-600">{scenario.target_audience}</p>
              </div>
            </div>

            {/*    */}
            {scenario.customer_persona && (
              <div className="border-t border-gray-200 pt-4 mb-4">
                <h4 className="text-sm font-medium text-gray-700 mb-2"> </h4>
                <div className="space-y-1 text-xs text-gray-600">
                  <div><span className="font-medium">:</span> {scenario.customer_persona.age_range || ''}</div>
                  <div><span className="font-medium">:</span> {scenario.customer_persona.gender || ''}</div>
                  <div><span className="font-medium">:</span> {scenario.customer_persona.characteristics || ''}</div>
                  <div><span className="font-medium">:</span> {scenario.customer_persona.interests || ''}</div>
                </div>
              </div>
            )}
            
            <div className="flex justify-between items-center text-sm text-gray-500">
              <span>: {new Date(scenario.created_at).toLocaleDateString('ko-KR')}</span>
              <span className="status-badge success"></span>
            </div>
          </div>
        ))}
      </div>

      {/*     */}
      {showCreateForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <h2 className="text-xl font-bold text-gray-900 mb-4">  </h2>
            
            <form onSubmit={handleCreateScenario} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <input
                    type="text"
                    value={formData.name}
                    onChange={(e) => setFormData({...formData, name: e.target.value})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <input
                    type="text"
                    value={formData.target_audience}
                    onChange={(e) => setFormData({...formData, target_audience: e.target.value})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({...formData, description: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  rows={3}
                  required
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                <input
                  type="text"
                  value={formData.business_goal}
                  onChange={(e) => setFormData({...formData, business_goal: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              {/*   */}
              <div className="border-t border-gray-200 pt-4">
                <h3 className="text-lg font-medium text-gray-900 mb-4">  ()</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.age_range || ''}
                      onChange={(e) => handlePersonaChange('age_range', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=": 25-35"
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.gender || ''}
                      onChange={(e) => handlePersonaChange('gender', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=": /"
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.characteristics || ''}
                      onChange={(e) => handlePersonaChange('characteristics', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":  ,   "
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.interests || ''}
                      onChange={(e) => handlePersonaChange('interests', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":  ,  "
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.pain_points || ''}
                      onChange={(e) => handlePersonaChange('pain_points', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":  ,  "
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.goals || ''}
                      onChange={(e) => handlePersonaChange('goals', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":     "
                    />
                  </div>
                </div>
                
                <div className="mt-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <textarea
                    value={formData.customer_persona?.behavior_patterns || ''}
                    onChange={(e) => handlePersonaChange('behavior_patterns', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    rows={2}
                    placeholder=":  ,   "
                  />
                </div>
              </div>
              
              <div className="flex space-x-3 pt-4">
                <button
                  type="submit"
                  className="btn-primary flex-1"
                >
                  
                </button>
                <button
                  type="button"
                  onClick={() => setShowCreateForm(false)}
                  className="btn-secondary flex-1"
                >
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/*    */}
      {showEditForm && editingScenario && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <h2 className="text-xl font-bold text-gray-900 mb-4"> </h2>
            
            <form onSubmit={handleUpdateScenario} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <input
                    type="text"
                    value={formData.name}
                    onChange={(e) => setFormData({...formData, name: e.target.value})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <input
                    type="text"
                    value={formData.target_audience}
                    onChange={(e) => setFormData({...formData, target_audience: e.target.value})}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({...formData, description: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  rows={3}
                  required
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                <input
                  type="text"
                  value={formData.business_goal}
                  onChange={(e) => setFormData({...formData, business_goal: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              {/*   */}
              <div className="border-t border-gray-200 pt-4">
                <h3 className="text-lg font-medium text-gray-900 mb-4">  ()</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.age_range || ''}
                      onChange={(e) => handlePersonaChange('age_range', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=": 25-35"
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.gender || ''}
                      onChange={(e) => handlePersonaChange('gender', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=": /"
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.characteristics || ''}
                      onChange={(e) => handlePersonaChange('characteristics', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":  ,   "
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.interests || ''}
                      onChange={(e) => handlePersonaChange('interests', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":  ,  "
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.pain_points || ''}
                      onChange={(e) => handlePersonaChange('pain_points', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":  ,  "
                    />
                  </div>
                  
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2"></label>
                    <input
                      type="text"
                      value={formData.customer_persona?.goals || ''}
                      onChange={(e) => handlePersonaChange('goals', e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                      placeholder=":     "
                    />
                  </div>
                </div>
                
                <div className="mt-4">
                  <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
                  <textarea
                    value={formData.customer_persona?.behavior_patterns || ''}
                    onChange={(e) => handlePersonaChange('behavior_patterns', e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                    rows={2}
                    placeholder=":  ,   "
                  />
                </div>
              </div>
              
              <div className="flex space-x-3 pt-4">
                <button
                  type="submit"
                  className="btn-primary flex-1"
                >
                  
                </button>
                <button
                  type="button"
                  onClick={() => {
                    setShowEditForm(false);
                    setEditingScenario(null);
                    setFormData({
                      name: '',
                      description: '',
                      business_goal: '',
                      target_audience: '',
                      customer_persona: ScenarioService.createDefaultPersona()
                    });
                  }}
                  className="btn-secondary flex-1"
                >
                  
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default ScenarioManager; 