import React from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

interface ScenarioPerformanceData {
  scenario_name: string;
  conversion_rate: number;
  total_users: number;
}

interface ScenarioComparisonChartProps {
  data: ScenarioPerformanceData[];
}

const ScenarioComparisonChart: React.FC<ScenarioComparisonChartProps> = ({ data }) => {
  if (!data || data.length === 0) {
    return (
      <div className="text-center py-8 text-gray-500">
           .
      </div>
    );
  }

  const chartData = {
    labels: data.map(item => item.scenario_name),
    datasets: [
      {
        label: ' (%)',
        data: data.map(item => item.conversion_rate),
        backgroundColor: 'rgba(59, 130, 246, 0.8)',
        borderColor: 'rgb(59, 130, 246)',
        borderWidth: 1,
        borderRadius: 4,
        borderSkipped: false,
      },
      {
        label: '  ( )',
        data: data.map(item => item.total_users / 100),
        backgroundColor: 'rgba(16, 185, 129, 0.8)',
        borderColor: 'rgb(16, 185, 129)',
        borderWidth: 1,
        borderRadius: 4,
        borderSkipped: false,
        yAxisID: 'y1',
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    interaction: {
      mode: 'index' as const,
      intersect: false,
    },
    plugins: {
      legend: {
        position: 'top' as const,
        labels: {
          usePointStyle: true,
          padding: 20,
        },
      },
      tooltip: {
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        titleColor: '#fff',
        bodyColor: '#fff',
        borderColor: 'rgba(59, 130, 246, 0.5)',
        borderWidth: 1,
        cornerRadius: 8,
        callbacks: {
          label: function(context: any) {
            if (context.datasetIndex === 0) {
              return `: ${context.parsed.y.toFixed(1)}%`;
            } else {
              return ` : ${(context.parsed.y * 100).toLocaleString()}`;
            }
          },
        },
      },
    },
    scales: {
      x: {
        grid: {
          display: false,
        },
        ticks: {
          color: '#6b7280',
          font: {
            size: 12,
          },
          maxRotation: 45,
        },
      },
      y: {
        type: 'linear' as const,
        display: true,
        position: 'left' as const,
        grid: {
          color: 'rgba(0, 0, 0, 0.05)',
        },
        ticks: {
          color: '#6b7280',
          font: {
            size: 12,
          },
          callback: function(value: any) {
            return value + '%';
          },
        },
        title: {
          display: true,
          text: ' (%)',
          color: '#6b7280',
          font: {
            size: 12,
          },
        },
      },
      y1: {
        type: 'linear' as const,
        display: true,
        position: 'right' as const,
        grid: {
          drawOnChartArea: false,
        },
        ticks: {
          color: '#6b7280',
          font: {
            size: 12,
          },
          callback: function(value: any) {
            return (value * 100).toLocaleString();
          },
        },
        title: {
          display: true,
          text: ' ',
          color: '#6b7280',
          font: {
            size: 12,
          },
        },
      },
    },
  };

  //   
  const sortedByConversion = [...data].sort((a, b) => b.conversion_rate - a.conversion_rate);
  const sortedByUsers = [...data].sort((a, b) => b.total_users - a.total_users);

  return (
    <div>
      {/*  */}
      <div className="h-64 mb-6">
        <Bar data={chartData} options={options} />
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/*   */}
        <div className="bg-gray-50 rounded-lg p-4">
          <h4 className="font-medium text-gray-900 mb-3"> </h4>
          <div className="space-y-2">
            {sortedByConversion.map((scenario, index) => (
              <div key={scenario.scenario_name} className="flex items-center justify-between">
                <div className="flex items-center">
                  <div className={`w-6 h-6 rounded-full flex items-center justify-center text-xs font-bold mr-3 ${
                    index === 0 ? 'bg-yellow-100 text-yellow-800' :
                    index === 1 ? 'bg-gray-100 text-gray-800' :
                    index === 2 ? 'bg-orange-100 text-orange-800' :
                    'bg-gray-50 text-gray-600'
                  }`}>
                    {index + 1}
                  </div>
                  <span className="text-sm font-medium text-gray-900 truncate">
                    {scenario.scenario_name}
                  </span>
                </div>
                <span className="text-sm font-bold text-primary-600">
                  {scenario.conversion_rate.toFixed(1)}%
                </span>
              </div>
            ))}
          </div>
        </div>

        {/*    */}
        <div className="bg-gray-50 rounded-lg p-4">
          <h4 className="font-medium text-gray-900 mb-3">  </h4>
          <div className="space-y-2">
            {sortedByUsers.map((scenario, index) => (
              <div key={scenario.scenario_name} className="flex items-center justify-between">
                <div className="flex items-center">
                  <div className={`w-6 h-6 rounded-full flex items-center justify-center text-xs font-bold mr-3 ${
                    index === 0 ? 'bg-yellow-100 text-yellow-800' :
                    index === 1 ? 'bg-gray-100 text-gray-800' :
                    index === 2 ? 'bg-orange-100 text-orange-800' :
                    'bg-gray-50 text-gray-600'
                  }`}>
                    {index + 1}
                  </div>
                  <span className="text-sm font-medium text-gray-900 truncate">
                    {scenario.scenario_name}
                  </span>
                </div>
                <span className="text-sm font-bold text-green-600">
                  {scenario.total_users.toLocaleString()}
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ScenarioComparisonChart; 